function changeOrderTab($this) {
    event.preventDefault();

    var
        href = $this.attr('href'),
        urlPart = href.replace('#', '');

    var currentUrl = window.location.href;
    var url = new URL(currentUrl);
    url.searchParams.set("tab", urlPart);
    var newUrl = url.href;

    window.location.href = newUrl;
}


